<template>
  <div class="card p-3">
    <div class="text-center">
      <img class="img-fluid" :src="item.logo" width="100">
      <div class="card-body">
        <h5 class="card-title">{{ item.name}}</h5>
        <small class="card-text">{{ item.desc}}</small>
				<div>
        <a :href="item.link" target="_blank">
          <img class="spacing" src="https://image.flaticon.com/icons/png/512/35/35920.png" width="30">
        </a>
				<a :href="item.link" target="_blank">
          <img class="spacing" src="https://theme.zdassets.com/theme_assets/604014/56a3e3bdd1ab65f1a38771e7aa26605376a88e1c.png" width="30">
        </a>
				<a :href="item.link" target="_blank">
          <img class="spacing" src="http://www.sanjaytechnologies.org/assets/admin/img/videoicon.gif" width="30">
        </a>
				</div>
      </div>
      <div>
        <span
          v-for="(value, key) in item.tag"
          :key="key"
          :class="`badge badge-${tags[key]}`"
        >{{ value }}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // list of tags to giving each tag a different color
      tags: {
        Prepare: "primary",
        Sign: "secondary",
        Act: "success",
        Manage: "danger",
        Integrate: "warning",
        Industry: "info"
      }
    };
  },
  props: {
    // this component expects a prop of type object
    item: {
      type: Object,
      required: true
    }
  }
};
</script>




